import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            tabData:[ '个人服务包', '团体服务包' ],
            tabIndex: 0,
            page: 1,
            haveMore: true,
            list: [],
            user: this.$store.state.userInfo,
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        changeTab(idx){
            this.tabIndex = idx
            this.page = 1
            this.haveMore = true
            this.pull(false)
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        pull(h){
            this.$post('PlatFormAPI/ServicePackItem/GetServicePackRecordsByDrId',this.$pick({
                DrId: this.user.Data.User.DoctorId,
                ServiceType: this.tabIndex == 0 ? 1 : 2,
                PageIndex: this.page,
                PageSize: 15,
            })).then(o => {
                this.pageShow = true
                if (!o.ReturnData || o.ReturnData.length < 15){
                    this.haveMore = false
                }
                else {
                    this.haveMore = true
                }
                
                if (h){
                    o.ReturnData.forEach(v => {
                        this.list.push(v)
                    })
                }
                else {
                    this.list = o.ReturnData
                }
            })
        }
    }
}



